<template>
  <div class="order-detail">
    <div class="order-detail__status-section">
      <el-steps
        v-if="tradeStatus !== 'CLOSED'"
        finish-status="success"
        :active="activeSteps"
        simple
        class="order-detail__status-section__steps"
      >
        <el-step title="用户下单" />
        <el-step title="待用户付款" />
        <el-step title="交易成功" />
      </el-steps>
      <card
        class="order-detail__card"
        :title="'当前交易状态：' + tradeStatusFilter(tradeStatus)"
      >
        <div>
          <div class="order-detail__card__description">
            {{ tradeStatus | statusDescription(orderCloseReason) }}
          </div>
          <close-trade
            v-if="tradeStatus === 'PENDING'"
            :id="id"
            v-model="dialogVisible"
            @closeTradeAfter="getOrderDetail"
          >
            <el-button
              class="close-trade__btn"
              type="primary"
              size="small"
              @click="dialogVisible = true"
              >关闭交易</el-button
            >
          </close-trade>
        </div>
      </card>
    </div>
    <el-tabs
      v-model="activeName"
      type="border-card"
      class="order-detail__other-info"
    >
      <el-tab-pane label="订单信息" name="order-info">
        <order-info
          :cur-order-info="curOrderInfo"
          :tradeStatus="tradeStatus"
          @activeService="activeService"
        />
      </el-tab-pane>
      <el-tab-pane
        v-if="
          curOrdertype === 'FdServiceType' &&
            curOrderInfo.contract &&
            curOrderInfo.contract.status
        "
        name="activation-info"
      >
        <span slot="label" class="order-detail__other-info__el-tab-pane-label"
          >家庭医生服务信息</span
        >
        <server-info :cur-server-info="curServerInfo" />
      </el-tab-pane>
      <el-tab-pane
        v-if="curOrdertype === 'InquiryServiceType'"
        label="问诊信息"
        name="interviews-info"
      >
        <span slot="label" class="order-detail__other-info__el-tab-pane-label"
          >单次问诊信息</span
        >
        <SingleaskInfo :cur-server-info="curQuestionInfo" />
      </el-tab-pane>
    </el-tabs>
    <el-dialog
      title="确认激活服务提示"
      :visible.sync="dialogActiveVisible"
      width="30%"
    >
      <div style="margin-bottom: 30px">确认要激活该用户套餐吗？</div>
      <div>激活后服务立即生效，不可撤销</div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogActiveVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmActive">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getOrderDetail as getOrderDetailApi, activeTrade } from '@/api/order'
import Card from '@/components/Card'
import { CloseTrade, OrderInfo, ServerInfo, SingleaskInfo } from '../components'

import { tradeStatusMixin } from '../mixins'

export default {
  components: { Card, OrderInfo, CloseTrade, ServerInfo, SingleaskInfo },
  filters: {
    statusDescription(value, msg) {
      switch (value) {
        case 'PENDING':
          return '用户已下单，等待支付'
        case 'SUCCESS':
          return '交易已成功，如果用户提出售后要求，请积极用户与协商，做好售后服务。'
        case 'CLOSED':
          return `取消原因：${msg}`
        default:
          if (value) {
            throw new Error(`无法识别的订单状态: ${value}`)
          } else {
            return ''
          }
      }
    }
  },
  mixins: [tradeStatusMixin],
  data() {
    return {
      dialogActiveVisible: false,
      tradeStatus: '',
      orderCloseReason: '',
      activeName: 'order-info',
      curOrderInfo: {
        contract: {}
      },
      curServerInfo: {},
      curQuestionInfo: [],
      dialogVisible: false,
      curOrdertype: ''
    }
  },
  computed: {
    activeSteps() {
      if (this.tradeStatus === 'PENDING') return 2
      else if (this.tradeStatus === 'SUCCESS') return 3
      else return -1
    },
    id() {
      return this.$route.params.id
    }
  },
  created() {
    this.getOrderDetail()
  },
  methods: {
    activeService() {
      this.dialogActiveVisible = true
    },
    confirmActive() {
      const id = this.curServerInfo._id
      activeTrade(id).then(res => {
        this.$message.success('激活成功')
        this.dialogActiveVisible = false
        this.getOrderDetail()
      })
    },
    async getOrderDetail() {
      const { data } = await getOrderDetailApi(this.id)
      this.tradeStatus = data.trade_status
      this.orderCloseReason = data.close_reason
      this.curOrdertype = data.order.order_type
      this.curOrderInfo = {
        order: data.order,
        user: data.user
      }
      if (data.contracts.length === 0) {
        return
      }
      this.curOrderInfo.order._id = this.id
      this.curOrderInfo.contract = data.contracts[0]

      this.curServerInfo = data.contracts[0]
      this.curQuestionInfo = data.contracts
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.order-detail {
  &__status-section {
    .el-step {
      &__head.is-success {
        color: $activeColor;
        border-color: #{$activeColor};
      }
      &__icon.is-text {
        border-width: 1px;
      }
      &__title {
        &.is-process {
          font-weight: normal;
        }
        &.is-success {
          color: #{$activeColor};
        }
      }
    }
  }
  &__card {
    min-height: 182px;
  }
  &__other-info {
    border: 1px solid #ebeef5;
    border-radius: 4px;
    box-shadow: none;
    &.el-tabs--border-card > .el-tabs__header .el-tabs__item {
      color: #{$textLightBlock};
    }
    &.el-tabs--border-card > .el-tabs__header .el-tabs__item.is-active {
      color: #409eff;
      border-right-color: #f5f7fa;
      border-left-color: #f5f7fa;
    }
    &.el-tabs--border-card > .el-tabs__content {
      padding-top: 0;
      padding-right: 20px;
      padding-bottom: 0;
      padding-left: 20px;
    }
    .el-tabs {
      &__nav-wrap {
        &::after {
          height: 1px;
        }
      }
      &__item {
        height: 50px;
        line-height: 50px;
        font-weight: 400;
      }
      &__header {
        margin-bottom: 0;
        border-bottom: none;
      }
    }
    & &__service-info {
      padding-right: 0;
      padding-left: 0;
      border-width: 0;
      border-radius: 0;
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';

.order-detail {
  &__status-section {
    margin-bottom: 30px;
    &__steps {
      margin-bottom: 20px;
    }
    &__status-card {
      &__status {
        @include ff-medium;

        font-size: 16px;
        color: #{$textDeepBlock};
        letter-spacing: 0;
      }
    }
  }
  &__card {
    &__description {
      margin-bottom: 24px;
    }
  }
}
</style>
